import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'	
export default {
	data() {
		return {
			hasFlag:true,
			page:0,
			msgType:'',
			msgList:[],
			firstLoad:true,//第一次加载 或下拉刷新时为true
			isEmpty:false
		};
	},
	components:{
		uniLoadMore
	},
	methods:{
		// 获取消息列表
		getMsgList(){
			if(!this.hasFlag) return //没有更多了
			this.page = ++ this.page
			this.$fetch(this.$api.sysMsgList,{msgType:this.msgType,pageNum:this.page,pageSize:10},'POST','form').then(res =>{
				res.rows.forEach(item =>{
					item.createTime = this.$dayjs(item.createTime).format('YYYY-MM-DD')
					item.text = this.filterHTMLTag(item.content)
				})
				this.msgList = [...this.msgList,...res.rows]
				this.hasFlag  = 10*this.page < res.total
				if(this.firstLoad){
					this.firstLoad = false
					this.bindMsg()
				}
			})
		},
		// 获取富文本中的文字
		filterHTMLTag (msg) {
			var msg = msg.replace(/<\/?[^>]*>/g, ''); //去除HTML Tag
			msg = msg.replace(/[|]*\n/, '') //去除行尾空格
			msg = msg.replace(/&nbsp;/ig, ''); //去掉npsp
			return msg;
		},
		// 切换tab
		changeTab(index){
			this.page = 0
			this.hasFlag = true
			this.msgList = []
			if(index == 0){
				this.msgType = ''
			}else if(index == 1){
				this.msgType = 0
			}else if(index == 2){
				this.msgType = 1
			}
			this.tabCurrentIndex = index
			this.getMsgList()
		},
		// 绑定已读
		async bindMsg(){
			let msgId = this.msgList[0].id
			let res  = await this.$fetch(this.$api.bindUserMsg,{msgId:msgId},'POST','form')
		},
		// 查看详情
		handleOpenDetail(content,title){
			uni.setStorageSync('msgContent',content)
			uni.navigateTo({
				url:'messageDetail?title='+title
			})
		}
	},
	onLoad() {
		// this.getMsgList()
		setTimeout(()=>{
			this.isEmpty = true
		},2000)
	},
	onReachBottom() {
		if(this.hasFlag){
			this.getMsgList()
		}
	},
	onPullDownRefresh() {
		this.page = 0 
		this.hasFlag = true
		this.firstLoad = true
		this.msgList = []
		uni.stopPullDownRefresh()
		this.getMsgList()
	}
}
